.feedback-container{
    display:flex;
    justify-content:space-evenly;
    background-color:whitesmoke;
    flex-basis:33%;
    flex-wrap:wrap;
}

/* margin passed as input into cards component */
#feedback-item{
    margin:1%;
    width:30%;
    
}

@media screen and (max-width:600px) 
{
    #feedback-item{
        width:100%;
    }
}


@media screen and (min-width:600px) and (max-width:900px) 
{
    #feedback-item{
        width:70%;
    }
}